<template>
  <div class="dinfo">
    <div class="ztree">
      <ul id="treeDemo" class="ztree" @select="zTreeOnClick"></ul>
    </div>
    <div>
      <div class="bg">
        <div class="dinfotitle">
          <span> {{ title }}传感器 </span>
          <!-- <el-button @click="openDialog">添加设备类型</el-button> -->
        </div>
        <DxDataGrid
          :data-source="datalist"
          :allow-column-resizing="true"
          :show-column-lines="false"
          :auto-expand-all="false"
          :show-row-lines="true"
          :column-auto-width="true"
          :focused-row-enabled="true"
          :auto-navigate-to-focused-row="true"
          width="calc(240px + 50vw)"
          key-expr="key"
          class="main_table"
          style="height: calc(335px + 42vh);width:calc(240px + 50vw)"
        >
          <DxColumn data-field="id" alignment="center" caption="ID" />
          <DxColumn data-field="mark" alignment="center" caption="设备编码" />
          <DxColumn
            data-field="device_name"
            alignment="center"
            caption="设备名称"
          />
          <DxColumn
            data-field="position"
            alignment="center"
            caption="设备安装位置   "
          />
          <DxColumn data-field="indate" alignment="center" caption="入库时间" />
          <!-- <DxColumn data-field="isonline" alignment="center" caption="是否在线" /> -->
          <!-- <DxPaging :page-size="pagesize" /> -->
          <!-- <DxPager :visible="true" :allowed-page-sizes="pageSizes" :display-mode="displayMode"
                    :show-page-size-selector="showPageSizeSelector" :show-info="showInfo"
                    :show-navigation-buttons="showNavButtons" /> -->
          <DxScrolling use-native="false" />
        </DxDataGrid>
        <el-pagination
          :page-size="pagesize"
          :pager-count="5"
          :page-sizes="[10, 20, 30, 40]"
          @size-change="handleSizeChange"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
          @current-change="handleCurrentChange"
        >
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import dinfo from "./dinfo";
export default dinfo;
</script>
<style scoped>
@import url("./dinfo.css");
@import url("../../assets/css/houtaicommon.css");
</style>



